.svg-image-header-logo, .svg-image-arrow-down {
  background: url("../images/sprites/main.svg") no-repeat;
}

.svg-image-arrow-down {
  background-position: 0 100%;
  background-size: 977.7777777778% auto;
  width: 18px;
}

.svg-image-arrow-down:before {
  content: "";
  display: block;
  font-size: 0;
  line-height: 0;
  padding-top: 116.6666666667%;
}

.svg-image-arrow-down-dims {
  width: 18px;
  height: 21px;
}

.svg-image-header-logo {
  background-position: 0 0;
  background-size: 100% auto;
  width: 176px;
}

.svg-image-header-logo:before {
  content: "";
  display: block;
  font-size: 0;
  line-height: 0;
  padding-top: 36.3636363636%;
}

.svg-image-header-logo-dims {
  width: 176px;
  height: 64px;
}

.image-by-height {
  width: auto;
}

.image-by-height:before {
  content: none;
}

.image-by-height > svg {
  height: inherit;
}

.image-by-height > img {
  height: inherit !important;
  vertical-align: top !important;
  margin: 0 !important;
  padding: 0 !important;
  max-width: none !important;
  max-height: none !important;
  border: 0 !important;
  opacity: 0 !important;
}

* {
  box-sizing: border-box;
}

body {
  display: flex;
  flex-direction: column;
  background: #FFF;
  font-family: "Helvetica", Arial, sans-serif;
  width: 100%;
  height: 100%;
  min-height: 100vh;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.container {
  width: 100%;
  max-width: 850px;
  min-width: 320px;
  margin: 0 auto;
  padding: 0 40px;
}

:root {
  --color-dark: #0c2742;
  --color-blue: #1b75bc;
  --color-light-blue: #eef2f5;
  --color-white: #fff;
  --color-yellow: #f9ee8e;
  --color-green: #02ad23;
}

.first {
  position: relative;
  display: flex;
  flex-direction: column;
  width: 100%;
  height: 100%;
  min-height: 100vh;
}

.first__bg {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: -1;
  overflow: hidden;
}
.first__bg video {
  position: absolute;
  top: 0;
  left: 0;
  min-width: 100%;
  min-height: 100%;
}

.intro {
  flex-grow: 1;
}
.intro .container {
  max-width: 1328px;
}

.intro__wrapper {
  height: calc(100vh - 90px);
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  padding: 170px 0 100px 27px;
}

.intro__title {
  font-family: "Oswald", sans-serif;
  margin: 0;
  font-weight: 400;
  font-size: 120px;
  line-height: 100px;
  letter-spacing: 0.4px;
  color: var(--color-white);
  text-shadow: 0px 4px 10px rgba(0, 0, 0, 0.2);
}
.intro__title p {
  margin: 0;
}
.intro__title .intro__static {
  margin-bottom: 24px;
}
.intro__title .intro__frame {
  height: 120px;
  overflow: hidden;
}
.intro__title .intro__dynamic {
  color: var(--color-yellow);
  transform: translateY(120px);
  opacity: 0;
  animation: TextSlider 10s linear 0.5s infinite;
  animation-fill-mode: forwards;
}
.intro__title .intro__dynamic p {
  margin-bottom: 20px;
}
.intro__title .intro__dynamic p:last-child {
  margin-bottom: 0;
}

.intro__info {
  flex-grow: 1;
  display: flex;
  justify-content: space-between;
  align-items: flex-end;
}

.intro__about {
  max-width: 596px;
  font-family: "Poppins", sans-serif;
  font-weight: 400;
  font-size: 28px;
  line-height: 38px;
  letter-spacing: 0.4px;
  color: rgba(255, 255, 255, 0.8);
}

.intro__scroll {
  display: flex;
  align-items: center;
  transform: rotate(90deg);
  animation: Rotate 0.5s linear 0.5s;
  animation-fill-mode: forwards;
  opacity: 0;
}
.intro__scroll p {
  margin-left: 12px;
  font-family: "Oswald", sans-serif;
  font-weight: 500;
  font-size: 18px;
  line-height: 38px;
  letter-spacing: 0.4px;
  text-transform: uppercase;
  color: rgba(255, 255, 255, 0.8);
}

@keyframes Rotate {
  0% {
    transform: rotate 90deg;
    opacity: 0;
  }
  50% {
    opacity: 1;
  }
  100% {
    transform: rotate(0);
    opacity: 1;
  }
}
@keyframes TextSlider {
  0% {
    opacity: 0;
    transform: translateY(120px);
  }
  2% {
    opacity: 1;
  }
  8% {
    transform: translateY(0);
  }
  16% {
    transform: translateY(0);
  }
  24% {
    transform: translateY(-120px);
  }
  32% {
    transform: translateY(-120px);
  }
  40% {
    transform: translateY(-240px);
  }
  98% {
    opacity: 1;
  }
  100% {
    transform: translateY(-240px);
    opacity: 0;
  }
}
.header {
  font-family: "Oswald", sans-serif;
}
.header .container {
  max-width: 1400px;
}

.header__wrapper {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.header__logo {
  width: 174px;
  height: 62px;
}

.header__navbar {
  height: 90px;
}
.header__navbar .header__list {
  height: 100%;
  display: flex;
  align-items: flex-end;
  margin: 0;
  padding: 0;
  list-style-type: none;
}

.header__link {
  height: 100%;
  padding: 44px 11px 12px;
  border-bottom-left-radius: 3px;
  border-bottom-right-radius: 3px;
  color: var(--color-white);
}
.header__link:last-child {
  margin: 0;
}
.header__link.active {
  background-color: var(--color-blue);
}
.header__link a {
  display: block;
  padding: 5px 10px;
  cursor: pointer;
}

.header__appply {
  padding: 0;
  height: 56px;
  color: var(--color-blue);
  background-color: var(--color-white);
  border-radius: 3px;
}
.header__appply a {
  display: block;
  padding: 16px 24px;
}

.main {
  flex-grow: 1;
}

footer {
  padding: 20px;
}